import React from "react";
import about from "../../Assets/about";
import work from "../../Assets/experience";
import nontech from "../../Assets/work";
import { Row } from "react-bootstrap";
import "./About.css";
import { Link } from "react-router-dom";
const About = () => {
  document.addEventListener('DOMContentLoaded', function () {
    const hiddenElements = document.querySelectorAll('.hidden');
  
    const observer = new IntersectionObserver((entries) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          entry.target.classList.add('animate');
        } else {
          entry.target.classList.remove('animate');
        }
      });
    }, {
      threshold: 0.5 // Adjust this value as needed
    });
  
    hiddenElements.forEach(element => {
      observer.observe(element);
    });
  });
  
  return (
      <Row
        className="text-light d-flex flex-column row-content work-content"
      >
        <h1 className="d-flex justify-content-center align-items-center mt-3">
          Non-Tech
        </h1>
        {nontech.map((non) => (
          <div
            border="light"
            key={non.id}
            className="mt-5 row g-0 hidden"
          >
            <div className="col-md-6 mb-md-0 p-md-4  text-light">
              <img src={non.pic} className="w-100" alt={non.title} />
            </div>
            <div
              className="col-md-6 p-4 ps-md-0  text-light 
                  "
            >
              <h5 className="mt-0">{non.title}</h5>
              <h6>
                <i>{non.description1}</i>
              </h6>
              <p>
                <ul>
                  <li>{non.description2}</li>
                  <li>{non.description3}</li>
                  <li>{non.description4}</li>
                </ul>
              </p>
              <h6 className="text-danger">{non.note}</h6>
            </div>
          </div>
        ))}
        <h1 className="d-flex justify-content-center align-items-center mt-3 ">
          Projects
        </h1>
        {about.map((non) => (
          <div
            border="light"
            key={non.id}
            className="mt-5 row g-0  hidden"
          >
            <div className="col-md-6 mb-md-0 p-md-4 text-light">
              <img src={non.pic} className="w-100" alt={non.title} />
            </div>
            <div className="col-md-6 p-4 ps-md-0 text-light">
              <h5 className="mt-0">{non.title}</h5>
              <h6>
                <i>{non.description1}</i>
              </h6>
              <p>
                <ul>
                  <li>{non.description2}</li>
                  <li>{non.description3}</li>
                  <li>{non.description4}</li>
                </ul>
              </p>
              <h6>
                <a href={non.link}>Visit Now!</a>
              </h6>
              <h6 className="text-danger">{non.note}</h6>
            </div>
          </div>
        ))}
        {work.map((non) => (
          <div
            border="light"
            key={non.id}
            className="mt-5 row g-0  hidden"
          >
            <div className="col-md-6 mb-md-0 p-md-4  text-light">
              <img src={non.pic} className="w-100" alt={non.title} />
            </div>
            <div className="col-md-6 p-4 ps-md-0  text-light">
              <h5 className="mt-0">{non.title}</h5>
              <h6>
                <i>{non.description1}</i>
              </h6>
              <p>
                <ul>
                  <li>{non.duration}</li>
                  <li>{non.description2}</li>
                  <li>{non.description3}</li>
                  <li>{non.description4}</li>
                  <li>{non.description5}</li>
                  <li>{non.description6}</li>
                  <li>{non.description7}</li>
                </ul>
              </p>
              <h6>
                <a href={non.link}>Visit Now!</a>
              </h6>
              <h6 className="text-danger">{non.note}</h6>
            </div>
          </div>
        ))}
        <button
          style={{ "--delay": "0.4s" }}
          className="scale-in-center btn btn-primary d-inline-block"
          type="button"
        >
          <Link to={"/"} className="text-light text-unstyled">
            Home
          </Link>
        </button>
      </Row>
  );
};

export default About;
